//
// Logo
// ====================================

// Variables
$me-at-work-width:      450;

// Me alone
.me-wrapper {
  position: relative;
  width: 170px;
  height: 156px;
}

.me-inner {
  position: relative;
  overflow: hidden;
  width: 156px;
  height: 156px;
  border-radius: 50%;
  background-color: #fff;
}

.me {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 86px;
  height: 135px;
  margin-left: -43px;
  background: url(../svg/kamilla-portfolio-logo.svg);
}

.me-heart-wrapper {
  position: absolute;
  top: 15px;
  right: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #fff;
}

.me-heart {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  background: url(../svg/kamilla-portfolio-heart.svg);
}

// Me at work
.me-at-work-wrapper {
  max-width: $me-at-work-width#{px};
  margin: 125px auto 0;
}

.me-at-work {
  position: relative;
  max-height: 200px;
  @include clearfix();
}

.me-at-work-self {
  width: percentage(297 / $me-at-work-width);
  margin-left: percentage(10 / $me-at-work-width);
  float: left;
}

.me-at-work-cat {
  width: percentage(120 / $me-at-work-width);
  position: absolute;
  right: percentage(20 / $me-at-work-width);
  bottom: percentage(33 / 200) * -1; // 33 is height of tail
}

.me-at-work-table {
  img {
    @include center-block();
  }
}
